				bottom: false,
			},
			activePop: 'middle'
		};
	},
	components: {
		uniIcon
	},
	methods: {
		show: function(e) {
			var pos = e.currentTarget.dataset.position;
			console.log(pos);
			switch (pos) {
				case 'top':
					this.activePop = 'top'
					break
				case 'bottom':
					this.activePop = 'bottom'
					break
				default:
					this.activePop = 'middle'
			}
			this.showMask = true
			this.showState[this.activePop] = true
		},
		hide: function() {
			this.showMask = false
			this.showState[this.activePop] = false
		},
	}
}



